.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
form {
  border: 5px solid #969595;
  background-image: url("../src/images/register.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  }
.container
{
  border-radius: 10px;
  margin: 1% 30%;
  padding: 2% 5%;
  background-color: white;
  opacity: 0.7;
  text-align: center;
}
.btn-btn
{
  background-color: rgb(7, 7, 7);
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
  label
  {
    display: block;
    text-align: left;
    margin-left: 15%;
  }
input{
  width: 75%;
  padding: 8px 8px;
  margin: 8px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
  box-sizing: border-box;
  }
  h1 {
    text-align:center;
    font-size:18;
    }
